.App {
display: flex;
justify-content: center;
font-family:Arial, Helvetica, sans-serif ;
font-size: 25px;
flex-direction: column;
}
.body{
  margin: 0%;
  padding: 0%;
}
.titlesection{
  background-color: rgb(243, 12, 12);
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction:column;
  align-items: center;
  color: azure;
}
.titlesection input{
  width: 200px;
  height:40px ;
  border-radius: 10px;
  border:1px solid rgb(233, 9, 9);
  font-size: 18px;
  padding-left: 10px;
}
.titlesection button{
  width: 200px;
    height: 30px ;
  margin: 20px;
  border-radius: 10px;
 border: none;
  font-size: 18px;
  padding-left: 10px;
  background-color: rgb(45, 45, 122);
}
.Displaysection{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Displaysection h3, h4{
  margin: 5px;

}
.Displaysection img{
  width: 200px;
  
}
  